}
.option-container .each_group .weui-cells .weui-cell__bd {
      padding-right: 11px;
}
.option-container .captain-search-container {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1;
    margin-top: 5px;
    width: 100%;
    max-height: 120px;
    overflow: auto;
